<template>
 <div class="container"> <img alt="Vue logo" loading="lazy" src="../assets/logo.png" style="width: 10rem; height: 10rem"/> <h1 class="text-white">APP HECHO CON COMPOSITION API</h1> <Buscador @accion="obtenerStickers" class="mb-4"/> <div class="row"> <div class="col" v-for="sticker in stickers" :key="sticker.id"> <Card :data="sticker"/> </div></div></div>
</template>

<script>
import Card from "@/components/Card";
import Buscador from "@/components/Buscador";
//import Footer from "@/components/Footer";
import { ref } from "vue";

export default {
  name: "App",
  components: { Card, Buscador },

  setup() {
    const stickers = ref({});
    const search = ref("");

    const obtenerStickers = async (search = "DBZ") => {
      try {
        const res = await fetch(`https://api.giphy.com/v1/stickers/search?q=${search}&api_key=8DzXku6215DmtAiEngS54kVEfdzsa3Cu`);
        const data = await res.json();
        stickers.value = data.data;
        //console.log(data.data)
      } catch (error) {
        console.log(error);
      }
    };

    obtenerStickers();

    return { stickers, search, obtenerStickers };
  },
};
</script>

